*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  width: 100%;
  min-height: 100vh;
  background-color: whitesmoke;
  color: #202020;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  line-height: 1.75;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 700;
}

small,
.text-small {
  font-size: 0.833rem;
}

a {
  text-decoration: none;
}

main {
  display: flex;
  flex-wrap: wrap;
}

.entry {
  width: 100%;
  height: 400px;
  display: grid;
  place-items: center;
}

@media screen and (min-width: 800px) {
  .entry {
    width: 50%;
  }

  .wide {
    width: 100%;
  }
}
#current {
  background: seagreen;
}
#current div {
  width: 320px;
  height: 320px;
  position: relative;
}
#current div::before {
  content: "";
  position: absolute;
  width: 60%;
  height: 40%;
  top: 30%;
  left: 20%;
  background: linear-gradient(#393870, #393870) 62% 14%/13% 4%, linear-gradient(to right, #2b2b55 10%, transparent 10%) 90% 50%/45% 80%, linear-gradient(#2b2b55 8%, #393870 8%) 90% 50%/45% 80%, linear-gradient(whitesmoke, whitesmoke) 10% 50%/45% 80%, linear-gradient(#212121, #212121) 0% 0%/100% 100%;
  border-radius: 5px;
  background-repeat: no-repeat;
}
#current div::after {
  content: "";
  position: absolute;
  width: 75%;
  height: 3%;
  top: 69%;
  left: 12.5%;
  background: linear-gradient(silver, silver) 0% 0%/100% 100%;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}